<template>
  <div class="index">
    <menu-item></menu-item>
    <my-name name="DAVID COPANO" job="Desarrollador web y apps hbridas"></my-name>
    <about-item id="about"></about-item>
    <experience-item></experience-item>
    <education-item></education-item>
    <projects-item></projects-item>
    <skills-item></skills-item>
    <contact-item></contact-item>
    <footer-item></footer-item>
  </div>
</template>

<script>
import Menu from "./Menu.vue";
import MyName from "./MyName.vue";
import About from "./About.vue";
import Experience from "./Experience.vue";
import Education from "./Education.vue";
import Projects from "./Projects.vue";
import Skills from "./Skills.vue";
import Contact from "./Contact.vue";
import Footer from "./Footer.vue";

export default {
  name: "Index",
  components: {
    "menu-item": Menu,
    "my-name": MyName,
    "about-item": About,
    "experience-item": Experience,
    "education-item": Education,
    "projects-item": Projects,
    "skills-item": Skills,
    "contact-item": Contact,
    "footer-item": Footer
  },
  methods: {
    scrollToId(id) {
      console.log('scrollToId()');
      console.log(id);
      
      let element = document.getElementById(id);
      console.log(element);
      //document.getElementById(id).scrollIntoView();
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../styles/custom-bootstrap.scss";
@import "../../node_modules/bootstrap/scss/bootstrap.scss";
@import "../assets/font-awesome/css/font-awesome.min.css";

.index {
  .index__down {
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    text-align: center;
    z-index: 10;
    bottom: 15px;
    color: #fff;

    span {
      cursor: pointer;
      display: block;
      margin: 0 auto;
      width: 35px;
      height: 35px;
      border-radius: 50%;
      border: 3px solid rgba(53, 92, 125, 0.8);
      text-align: center;
      transition: 0.3s;

      i {
        animation: pulsate 1.5s ease;
        animation-iteration-count: 1;
        animation-iteration-count: infinite;
        padding-top: 5px;
        color: rgba(53, 92, 125, 0.8);
      }
    }

    span:hover {
      transform: scale(1.2);
    }
  }
}
</style>



