        <span class="education-date">Septiembre 2017 - Diciembre 2017</span>
        <h4>MF0241_2: Informacin y atencin al cliente/consumidor/usuario.</h4>
        <p>Hice un curso de 120 horas en el campus online de Audiols.</p>
    </div>
    <div class="education-block">
        <h3>IES Romero Vargas, Cdiz</h3>
        <span class="education-date">Septiembre 2014 - Junio 2017</span>
        <h4>CFGS - Desarrollo de Aplicaciones Web</h4>
        <p>Curs un Ciclo Formativo de Grado Superior de Desarrollo de Aplicaciones Web en este perodo, aqu fue donde empez todo :)</p>
    </div>
    <div class="education-block">
        <h3>IES Asta Regia, Cdiz</h3>
        <span class="education-date">Septiembre 2012 - Junio 2014</span>
        <h4>Bachillerato de Ciencias y Tecnologa</h4>
        <p>Curs el Bachillerato de Ciencias y Tecnologa en este perodo.</p>
    </div>
    <div class="education-block">
        <h3>IES Asta Regia, Cdiz</h3>
        <span class="education-date">Septiembre 2008 - Junio 2012</span>
        <h4>Graduado de la ESO</h4>
        <p>Curs el graduado de la ESO en este perodo.</p>
