<script>
export default {
  name: "Education"
};
</script>

<style lang="scss" scoped>
@import "../styles/custom-bootstrap.scss";
@import "../../node_modules/bootstrap/scss/bootstrap.scss";
@import "../assets/font-awesome/css/font-awesome.min.css";

#education {
  padding: 50px 15px 20px 15px;
  border-bottom: 1px solid #dcd9d9;
  text-align: center;

  .heading {
    color: #374054;
    margin-bottom: 50px;
    position: relative;
    display: inline-block;
    font-size: 2em;
    font-weight: 300;
    margin: 0 0 30px 0;
    margin-bottom: 30px;
  }

  .education-block {
    max-width: 700px;
    margin: 0 auto 30px auto;
    padding: 15px;
    border: 1px solid #dcd9d9;
    text-align: left;

    h3 {
      font-weight: 500;
      float: left;
      margin: 0;
      color: #374054;
    }

    h4 {
      color: #74808a;
      clear: both;
      font-weight: 500;
      margin: 0 0 15px 0;
    }

    p,
    ul {
      margin: 0;
      color: #74808a;
      font-size: 0.9em;
    }

    ul {
      padding: 0 0 0 15px;
    }

    span {
      color: #74808a;
      float: right;
    }
  }
}

@media screen and (max-width: 768px) {
  #education {
    .education-block {
      .education-date {
        margin: 10px 0 10px 0;
        float: left;
      }
    }
  }
}
</style>



