<template>
  <div class="my-name">
    <div class="my-name__container">
        <h1 class="title text-primary">{{name}}</h1>
        <h2 class="subtitle">{{job}}</h2>
    </div>
    <div class="my-name--overlay"></div>
  </div>
</template>

<script>
export default {
  name: "MyName",
  props: ["name", "job"]
};
</script>

<style lang="scss" scoped>
@import "../styles/custom-bootstrap.scss";
@import "../../node_modules/bootstrap/scss/bootstrap.scss";
@import "../assets/font-awesome/css/font-awesome.min.css";

.my-name {
  position: relative;
  height: 100vh;
  min-height: 500px;
  max-height: 1080px;
  background: url(../assets/lead-bg.jpg);
  background-size: auto auto;
  background-size: cover;
  padding: 15px;
  overflow: hidden;

  .my-name__container {
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;

    .title {
      color: #fff !important;
      font-weight: 900;
      font-size: 5em;
      text-transform: uppercase;
      letter-spacing: 0.05em;
      line-height: 0.9em;
      margin-bottom: 10px;
    }

    .subtitle {
      color: rgba(255, 255, 255, .5);
      font-weight: 500;
      font-size: 2.25em;
      margin-bottom: 15px;
      margin-top: 0;
    }
  }
}

.my-name--overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #355c7d;
  background: -webkit-linear-gradient(
    to right,
    rgba(192, 108, 132, 0.8),
    rgba(108, 91, 123, 0.8),
    rgba(53, 92, 125, 0.8)
  );
  background: linear-gradient(
    to right,
    rgba(192, 108, 132, 0.8),
    rgba(108, 91, 123, 0.8),
    rgba(53, 92, 125, 0.8)
  );
  z-index: 1;
}
</style>



