<template>
  <div id="experience" class="background-alt">
    <h2 class="heading">Experiencia</h2>
    <div id="experience-timeline">
        <div class="vtimeline-point" v-for="exp in experienceInfo.experience">
            <div class="vtimeline-icon">
                <i class="fa fa-map-marker"></i>
            </div>
            <div class="vtimeline-block">
                <span class="vtimeline-date">{{exp.date}}</span>
                <div v-bind:data-date="exp.date" class="vtimeline-content">
                    <h3>{{exp.employerName}}</h3>
                    <h4>{{exp.jobTitle}}</h4>
                    <p>
                        {{exp.description}}
                    </p>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import experience from "../data/experience.json";

export default {
  name: "Experience",
  data: () => {
    return {
      experienceInfo: experience
    };
  }
};
</script>

<style lang="scss" scoped>
@import "../assets/font-awesome/css/font-awesome.min.css";
@import "../styles/custom-bootstrap.scss";
@import "../../node_modules/bootstrap/scss/bootstrap.scss";

#experience {
  padding: 50px 15px;
  text-align: center;
  border-bottom: 1px solid #dcd9d9;
  background: #f2f2f5;

  .heading {
    color: #374054;
    position: relative;
    display: inline-block;
    font-size: 2em;
    font-weight: 300;
    margin: 0 0 30px 0;
  }

  #experience-timeline {
    margin: 30px auto 0 auto;
    position: relative;
    max-width: 1000px;

    &::before {
      position: absolute;
      content: "";
      top: 0;
      bottom: 0;
      left: 303px;
      right: auto;
      height: 100%;
      width: 3px;
      background: rgba(108, 91, 123, 1);
      z-index: 0;
    }

    &::after {
      position: absolute;
      content: "";
      width: 3px;
      height: 40px;
      background: linear-gradient(
        to bottom,
        rgba(108, 91, 123, 1),
        rgba(52, 152, 219, 0)
      );
      top: 100%;
      left: 303px;
    }

    .vtimeline-point {
      position: relative;
      display: block;
      margin-bottom: 30px;

      .vtimeline-icon {
        position: relative;
        color: #fff;
        width: 50px;
        height: 50px;
        background: rgba(108, 91, 123, 1);
        border-radius: 50%;
        float: left;
        z-index: 99;
        margin-left: 280px;

        i {
          display: block;
          font-size: 2em;
          margin-top: 10px;
        }
      }

      .vtimeline-block {
        .vtimeline-date {
          width: 260px;
          text-align: right;
          position: absolute;
          left: 0;
          top: 10px;
          font-weight: 300;
          color: #374054;
        }
      }

      .vtimeline-content {
        margin-left: 350px;
        background: #fff;
        border: 1px solid #e6e6e6;
        padding: 15px;
        border-radius: 3px;
        text-align: left;

        h3 {
          font-size: 1.5em;
          font-weight: 300;
          color: #374054;
          display: inline-block;
          margin: 0;
        }

        h4 {
          font-size: 1.2em;
          font-weight: 300;
          color: #7e8890;
          margin: 0 0 15px 0;
        }

        p {
          color: #74808a;
          font-size: 0.9em;
          margin: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  #experience {
    #experience-timeline {
      .vtimeline-point {
        .vtimeline-icon {
          top: 8px;
        }
        .vtimeline-block {
          .vtimeline-date {
            margin-top: 8px;
            margin-left: 8px;
          }
        }
        .vtimeline-content {
          margin-left: 0 !important;
        }
      }
    }
  }
}
</style>



