<template>
    <div :id="id">
        <div class="container">
            <div class="row">
                <div class="col-md-4">
                    <h2 class="heading">Sobre m</h2>
                </div>
                <div class="col-md-8">
                    <p>
                        Soy David Copano Jimnez, soy desarrollador web y de aplicaciones hbridas. Tengo 21 aos y soy una persona interesada en aprender las ltimas tecnologas y patrones de diseo utilizados en el mercado. Me gusta trabajar en equipo, ya que pienso que es la mejor forma de aprender y cooperar para poder completar los proyectos :)
                    </p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: "About",
  props: ['id']
};
</script>

<style lang="scss" scoped>
@import "../styles/custom-bootstrap.scss";
@import "../../node_modules/bootstrap/scss/bootstrap.scss";
@import "../assets/font-awesome/css/font-awesome.min.css";

#about {
  padding: 75px 15px;
  border-bottom: 1px solid #dcd9d9;

  .heading {
    position: relative;
    display: inline-block;
    font-size: 2em;
    font-weight: 300;
    margin: 0 0 30px 0;

    &::after {
      position: absolute;
      content: "";
      top: 100%;
      height: 1px;
      width: 50px;
      left: 0;
      right: 0;
      margin: 0 auto;
      background: #355c7d;
      background: -webkit-linear-gradient(
        to right,
        rgba(192, 108, 132, 0.8),
        rgba(108, 91, 123, 0.8),
        rgba(53, 92, 125, 0.8)
      );
      background: linear-gradient(
        to right,
        rgba(192, 108, 132, 0.8),
        rgba(108, 91, 123, 0.8),
        rgba(53, 92, 125, 0.8)
      );
    }
  }

  p {
    color: #74808a;
    margin: 0;
  }
}
</style>



