<template>
  <div id="skills">
    <h2 class="heading">Tengo conocimientos en...</h2>
    <ul>
      <li>HTML5</li>
      <li>CSS3</li>
      <li>JavaScript</li>
      <li>PHP</li>
      <li>MySQL</li>
      <li>Symfony</li>
      <li>NPM</li>
      <li>TypeScript</li>
      <li>SASS</li>
      <li>LESS</li>
      <li>Ionic 3</li>
      <li>Angular</li>
      <li>Vue.JS</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Skills"
};
</script>

<style lang="scss" scoped>
#skills {
  padding: 50px 15px;
  text-align: center;

  h2 {
    color: #374054;
    margin-bottom: 50px;
    position: relative;
    display: inline-block;
    font-size: 2em;
    font-weight: 300;
    margin: 0 0 30px 0;
    margin-bottom: 30px;
  }

  ul {
    display: block;
    margin: 0 auto;
    padding: 0;
    max-width: 800px;

    li {
      display: inline-block;
      margin: 7px;
      padding: 5px 10px;
      color: #374054;
      background: #e4e4ea;
      list-style: none;
      cursor: default;
      font-size: 1.2em;
    }
  }
}
</style>



