<template>
    <footer id="footer">
    <div class="container">
        <div class="row">
            <div class="col-sm-5 copyright">
                <p>
                    David Copano
                </p>
            </div>
            <div class="col-sm-2 top">
                <span id="to-top">
                    <i class="fa fa-chevron-up" aria-hidden="true"></i>
                </span>
            </div>
            <div class="col-sm-5 social">
                <ul>
                    <li>
                        <a href="https://github.com/davidcopano/davidcopano.github.io-dev" target="_blank" title="Ver cdigo fuente del portfolio">
                            <i class="fa fa-github" aria-hidden="true" style="color: black; font-size: 40px"></i>
                        </a>
                    </li>
                </ul>
            </div>
        </div>
    </div>
  </footer>
</template>

<script>
export default {
  name: "Footer"
};
</script>

<style lang="scss" scoped>
@import "../styles/custom-bootstrap.scss";
@import "../../node_modules/bootstrap/scss/bootstrap.scss";
@import "../assets/font-awesome/css/font-awesome.min.css";

footer {
  padding: 50px 0;

  .copyright {
    padding-top: 20px;

    p {
      margin: 0;
      color: #74808a;
    }
  }

  .top {
    text-align: center;

    span {
      cursor: pointer;
      display: block;
      margin: 15px auto 0 auto;
      width: 35px;
      height: 35px;
      border-radius: 50%;
      border: 3px solid #b9bfc4;
      text-align: center;
      display: none !important;
    }
  }

  .social {
    text-align: right;

    ul {
      margin: 5px 0 0 0;
      padding: 0;

      li {
        display: inline-block;
        font-size: 1.25em;
        list-style: none;

        a {
          display: block;
          color: #74808a;
          padding: 10px;

          &:hover {
            color: #355c7d;
          }
        }
      }
    }
  }
}
</style>



