<template>
  <div id="projects" class="background-alt">
    <h2 class="heading">Proyectos</h2>
    <div class="container">
        <div class="row">
            <div class="project shadow-large" v-for="project in projectsInfo.projects">
                <div class="project-image">
                    <img v-bind:src="project.imgUrl">
                </div>
                <div class="project-info">
                    <h3>{{project.name}} </h3>
                    <p>
                        {{project.description}}
                    </p>
                    <div class="links">
                      <!-- app links -->
                      <a v-bind:href="project.links.playStore" target="_blank" v-if="project.links.playStore !== null">
                        <img src="../assets/google-play.png">
                      </a>
                      <a v-bind:href="project.links.appStore" target="_blank" v-if="project.links.appStore !== null">
                        <img src="../assets/app-store.png">
                      </a>
                      <!-- web link -->
                      <a v-bind:href="project.links.web" target="_blank" v-if="project.links.web !== null" class="web-link">Ver proyecto</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import projects from "../data/projects.json";

export default {
  name: "Projects",
  data: () => {
    return {
      projectsInfo: projects
    };
  }
};
</script>

<style lang="scss" scoped>
@import "../assets/font-awesome/css/font-awesome.min.css";

#projects {
  padding: 50px 15px;
  border-bottom: 1px solid #dcd9d9;
  text-align: center;
  background: #f2f2f5;

  .heading {
    color: #374054;
    margin-bottom: 50px;
    position: relative;
    display: inline-block;
    font-size: 2em;
    font-weight: 300;
    margin: 0 0 30px 0;
    margin-bottom: 30px;
  }

  .container {
    .row {
      .project {
        position: relative;
        max-width: 900px;
        margin: 0 auto 30px auto;
        overflow: hidden;
        background: #fff;
        border-radius: 4px;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08), 0 3px 6px rgba(0, 0, 0, 0.15);

        .project-image {
          float: left;
          max-width: 300px;

          img {
            max-width: 100%;
            padding: 10px 0 10px 10px;
          }
        }

        .project-info {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          margin-left: 300px;
          padding: 15px;

          h3 {
            font-size: 1.5em;
            font-weight: 300;
            color: #374054;
            margin: 0 0 15px 0;

            .more-info {
              background: transparent;
              border: 0;
              vertical-align: middle;
              outline: none;
              border-radius: 50%;
              transition: 0.3s;

              i {
                padding: 10px 6px;
              }

              &:hover {
                opacity: 1;
                cursor: pointer;
                background: #6c5b7b;

                i {
                  color: white;
                }
              }
            }
          }

          p {
            color: #74808a;
            margin: 0 0 15px 0;
            font-size: 0.9em;
            padding: 0 16px;
          }

          .links {
            max-width: 200px;
            margin: 0 auto;

            a {
              img {
                max-width: 100%;
              }
            }
            .web-link {
              background-color: #6c5b7b;
              color: white;
              border-radius: 4px;
              text-decoration: none;
              position: relative;
              top: 15px;
              padding: 10px;
              transition: 0.3s;

              &:hover {
                background-color: darken(#6c5b7b, 10%);
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  #projects {
    .container {
      .project {
        .project-image {
          float: none !important;
          margin: 0 auto;

          img {
            margin: 16px 0 0 0 !important;
            padding: 0 !important;
          }
        }
        .project-info {
          position: static !important;
          top: auto !important;
          transform: initial !important;
          margin-left: 0 !important;
          min-height: 200px;

          .web-link {
            top: 5px !important;
          }
        }
      }
    }
  }
}
</style>



